@import url("https://fonts.googleapis.com/css?family=Raleway:800|Merriweather+Sans|Share+Tech+Mono");
.Layout {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.Layout .Content {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
  width: 100%;
  background: #eee;
  color: #fff;
  font-family: "Merriweather Sans", sans-serif;
}
.Layout .Content a {
  color: inherit;
  text-decoration: underline;
  cursor: pointer;
}

.LayoutLogin {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.LayoutLogin .Content {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
  width: 100%;
  background-image: url("/static/Comp1.gif");
  background-position: "center";
  background-size: "cover";
  background-repeat: "no-repeat";
  color: #fff;
  font-family: "Merriweather Sans", sans-serif;
}
.LayoutLogin .Content a {
  color: inherit;
  text-decoration: underline;
  cursor: pointer;
}